.wrapper {
  position: relative;
}

.app {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 1.5rem;
}
.app > * {
  margin-bottom: 1.5rem;
}
.app .title {
  align-self: center;
  font-size: 1.6rem;
  margin-top: 0.3rem;
  margin-bottom: 1.8rem;
  color: var(--white);
}
.app .search {
  position: relative;
  border-radius: 1.3rem;
  overflow: hidden;
}
.app .search__input {
  height: 3rem;
  width: 100%;
  padding: 1.8rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 1.3rem;
  border: none;
}
.app .search__btn {
  position: absolute;
  right: 0;
  height: 100%;
  background-color: var(--black);
  border: none;
  width: 18%;
  color: var(--white);
  font-weight: 700;
}
.app .info {
  padding: 1rem;
  background: var(--white);
  border-radius: 1.3rem;
}
.app .info__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.6rem;
}
.app .info__wrapper > * {
  margin-bottom: 1rem;
}
.app .info__label {
  color: var(--l-gray);
  font-weight: 700;
  font-size: 0.6rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  margin-bottom: 0.5rem;
}
.app .info__item {
  color: var(--d-gray);
  font-weight: 700;
  font-size: 1.2rem;
}

.background__img {
  position: absolute;
  z-index: -99;
  background: url("../../../images/pattern-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 20rem;
}